<template>
  <form>
    <div class="row">
      <div class="col">

        <div class="row">
          <div class="form-group col-md-6">
            <label for="date">Date</label>
            <datepicker id="date"></datepicker>
          </div>

          <div class="form-group col-md-4">
            <label for="transId">Start transaction id</label>
            <input type="number" id="transId" step="1" min="0" class="form-control form-control-sm" />
          </div>
        </div>

        <div class="row">
          <div class="form-group col-md-6">
              <label for="asset">From asset</label>
              <multiselect id="asset" v-model="value" :options="options" @input="onChange"></multiselect>
              <div class="invalid-feedback">Please, select source currency.</div>
          </div>

          <div class="form-group col-md-6">
            <label for="assets">To assets</label>
            <multiselect id="assets" v-model="value2" :options="options2" :multiple="true"></multiselect>
            <div class="invalid-feedback">Please, select target assets.</div>
          </div>
        </div>

        <div class="row">
          <div class="form-group col-md-6">
            <label for="clients">Clients</label>
            <multiselect id="clients" v-model="value3" :options="options3" :multiple="true"></multiselect>
            <div class="invalid-feedback">Please, select clients.</div>
          </div>
        </div>

        <div class="row mt-5 justify-content-center">
          <div class="form-group">
            <button type="submit" disabled="disabled" class="btn btn-lg btn-block btn-outline-success">Submit transactions</button>
          </div>
        </div>
      </div>
    </div>
  </form>
</template>


<script>
  import axios from 'axios'
  import Datepicker from 'vuejs-datepicker'
  import Multiselect from 'vue-multiselect'

  const API_V1_URL = process.env.API_V1_URL

  export default {
    components: {
      Datepicker,
      Multiselect,
    },
    data() {
      return {
        clients: {},
        currencies: {},
        value: null,
        options: [],
        value2: null,
        options2: [],
        value3: null,
        options3: [],
      }
    },
    created() {
      /**
       * Start loading
       */
      this.loading = true
      this.$store.dispatch('loadTotalPortfolios')
      /**
       * Load Portfolios
       */
        .then((data) => {
          var assets = this.$store.getters.getTPAssets
          var assetsArr = assets.map((el) => {
            // TODO
            var formatted = el.name + ' - ' + el.pieces
            this.currencies[formatted] = el.name
            return formatted
          })
          this.options = assetsArr
        })
        /**
         * Load Prices
         */
        .then(() => {
          axios.get(API_V1_URL + '/prices/?limit=1000&offset=0')
            .then((res) => {
              var arr = res.data.map(function(el) {
                return el.id + ' [ ' + el.symbol + ' ]'
              })
              this.options2 = arr
            })
        })
        /**
         * Load Clients
         */
        .then(() => {
          axios.get(API_V1_URL + '/users?role=client&offset=0&limit=1000')
            .then((res) => {
              res.data.forEach((el) => {
                this.clients[el.id] = el
              })
            })
        })
        /**
         * Stop loading
         */
        .finally(() => {
          this.loading = false
        })
    },
    methods: {
      /**
       * Load Clients who have the currency in their assets
       */
      onChange(value) {
        this.loading = true
        var _this = this
        var requests = Object.keys(this.clients).map((id) => {
          var ids = []
          this.clients[id].portfolios.forEach(function(portfolio) {
            ids.push(axios.get(API_V1_URL + '/portfolio/' + portfolio.id))
          })
          return ids
        })
        var reqs = [].concat(...requests)
        axios.all(reqs)
          .then((responses) => {
            var clients = []
            responses.forEach((res) => {
              for (let asset of res.data.assets) {
                if (asset.name === _this.currencies[value]) {
                  clients.push(this.clients[res.data.userId].firstName)
                }
              }
            })
            this.options3 = clients
            this.loading = false
          })
      },
    },
  }
</script>

<style src="vue-multiselect/dist/vue-multiselect.min.css"></style>



